@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
    body{
        @apply font-[Raleway];
    }
    li {
        @apply px-4; /*Spacing within the list */
        @apply cursor-pointer; /*Whenever you point the cursor it changes.*/
    }
}
